* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  font-family: "Nunito", sans-serif;
  text-rendering: optimizeLegibility;
  overflow-x: hidden;
  color: white;
}
html h2,
body h2 {
  font-size: 22px;
  font-weight: 300;
  color: black;
  text-transform: uppercase;
  text-align: center;
  padding: 4% 0;
}
html hr,
body hr {
  color: black;
  width: 70%;
  margin: auto; 
}

html p,
body p{
    color: black;
    font-size: 14px;
    padding: 6% 13%;
}

section{
  width: 100%;
}

.clearfix {
  zoom: 1;
}

.clearfix:after {
  content: ".";
  clear: both;
  display: -ms-inline-flexbox;
  height: 0;
}



