/*HEADER SECTION*/
#header{
    height: 700px;
    background-position: cover;
    background-attachment: fixed;
    background-size: 100%;
    background-repeat: no-repeat;
    font-family: "Nunito", sans-serif;
    background-size: cover;
    text-rendering: optimizeLegibility;
    overflow-x: hidden;
    color: white;
    margin: 0;
    font-family: 'Nunito', sans-serif;
}


#header h1{
    font-size:  100px;
    font-family: 'Nunito', sans-serif;
    margin-top: 15%;
    margin-left: 20%;

}

#header .subheading{
    font-size:  70px;
    font-family: 'Nunito', sans-serif;
    text-transform: none;
    color: white;
    font-weight: 500;
    text-align: left;
    margin-left: 20%;
    margin-top: -8%;
}
 
.logo {
    height: auto;
    width: 140px;
    float: left;
    padding-left: 3%;
    padding-top: 1%;
  }
  
  .logo-green {
    display: none;
  }
  
  .main-nav {
    list-style: none;
    float: right;
    text-decoration: none;
    font-weight: 400;
    font-size: 18px;
    color: white;
    margin-right: 2%;
    padding: 1%;
  }
  .main-nav li {
    font-family: "Nunito", sans-serif;
    text-decoration: none;
    display: inline-flex;
    justify-content: flex-end;
    padding: 5px;
    margin-top: 4%;
  }
  #ligueja {
    color: #faf4e8;
    background-color: #df4949;
    border-radius: 30px;
    padding: 5px;
  }
  #ligueja:hover {
    color: white;
    background-color: red;
    border-radius: 30px;
    text-decoration: none;
  }
  .main-nav #ligueja ion-icon {
    font-size: 14px;
  }
  .main-nav .navlink {
    padding: 8px;
    color: white;
    font-weight: 300;
  }


  .main-nav .navlink:after{
    display:block;
    content: '';
    color: #c6e47a;
    border-bottom: 1px solid white;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);  
    -webkit-transition: -webkit-transform 250ms ease-in-out;  
    transition: -webkit-transform 250ms ease-in-out;  
    transition: transform 250ms ease-in-out;  
    transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
  }

  .main-nav .navlink:hover:after{
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin:50% 100%;
            transform-origin:50% 100%;
  }

  .main-nav .navlink:last-of-type:hover{
    text-decoration: none;
  }

  .main-nav ion-icon {
    font-size: 20px;
  }

  
  /*STICKY NAV*/
  .sticky {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 16px;
    background-color: white;
    background-color: rgba(255, 255, 255, 0.98);
    box-shadow: 0 1px 2px #d9d9d9;
    z-index: 2;
  }
  .sticky .logo {
    display: none;
  }
  .sticky .logo-green {
    display: inline-flex;
    height: auto;
    width: 9%;
    padding-left: 3%;
    padding-top: 1%;
  }
  .sticky li {
    font-family: "Nunito", sans-serif;
    text-decoration: none;
    display: inline-flex;
    padding: 5px;
    margin-top: 20px;
  }
  .sticky li a {
    color: #555;
    font-size: 13px;
    border-bottom: none;
  }
  .sticky li i {
    font-size: 13px;
  }

/*MOBILE LINKS*/
.container-links {
  height: 100%;
  top: 0;
  padding-top: 3%;
  position: fixed;
  background-color: white;
  width: 50%;
  box-shadow: 2px 5px 15px #626060;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out; 
  z-index: 5;
}

.container-links.slide {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.continer-links ul{
  display: flex;
  flex-direction: column;

}
.container-links ul li {
  padding: 15%;
  border-bottom: 1px solid lightgrey;
}
.container-links ul .mobile-nav-link {
  color: #50811c;
  text-align: center;
  text-decoration: none;
  width: 100%;
  font-size: 14px;
}
.container-links ul .mobile-nav-link:hover {
  color: #c6e47a;
}

.mobile-nav-button {
  color: #50811c;
  top: -3%; 
  left: 90%;
  background-color: white;
  position: fixed;
  z-index: 3;
  border-radius: 1px;
  width: 40px;
}
.mobile-nav-button i{
  padding:2%;
}
.mobile-nav-button #menu-bars {
  font-size: 20px;
  font-weight: 500;
}

.mobile-nav-button #menu-close {
  font-size: 20px;
  font-weight: 500;
}
.mobile-nav-button:hover {
  color: white;
  background-color: #50811c;
  border-bottom: none;
}

